.side-tab {
  min-height: 600px;
  margin-top: 30px;
  background-color: white;
  flex: 1;
  a {
    color: #777777;
    text-decoration: none;
  }
  display: flex;
  flex-direction: row;
  .tabs {
    display: flex;
    flex-direction: column;
    flex-basis: 300px;
    align-items: stretch;

    .tab {
      text-align: center;
      margin: 5px;
      border-radius: 8px;
      padding: 15px 0;

      &.selected-true {
        background-color: #EEEEEE;
      }
    }
  }

  .tab-body {
    padding: 20px;
  }
}

.feature-container {
  display: flex;
  flex-direction: column;
}