.app-section {
  display: flex;
  flex-direction: row;
  justify-content: center;

  .app-section-description {
    //flex: 1;
    padding: 60px;
    display: flex;
    flex-direction: column;
    justify-content: center;

    .app-section-description-title {
      @import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap');
      font-family: "Source Sans Pro", Helvetica Neue, -apple-system, BlinkMacSystemFont, Helvetica, sans-serif;
    }

    .app-section-description-extra {
      padding: 30px 0;
      font-family: "Source Sans Pro", Helvetica Neue, -apple-system, BlinkMacSystemFont, Helvetica, sans-serif;
    }
  }

  .app-section-image {
    margin: 60px;
    //flex: 1;
    //box-shadow: 0 0 15px #000;
    border-radius: 10px;
    box-sizing: border-box;
  }
}

.app-image-container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;

  img {
    box-shadow: rgba(84, 70, 35, 0.15) 0px 2px 8px, rgba(84, 70, 35, 0.15) 0px 1px 3px;
    border-radius: 10px;
  }
}