@import url(https://fonts.googleapis.com/css?family=Ultra&display=swap);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-header{background-color:white;display:flex;flex-direction:row;justify-content:space-between;padding:0 300px}.app-header .app-logo{color:black;text-decoration:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;display:flex;flex-direction:row;align-items:center}.app-header .app-logo .app-logo-img{flex:0 0 60px;padding:10px 20px}.app-header .app-logo .app-logo-name{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;font-family:'Ultra', serif;font-size:40px}.app-header .app-actions{padding:20px;flex:1 1;display:flex;justify-content:flex-end}.app-header .app-actions a{padding:20px;color:#000;text-decoration:none}.app-header .app-actions a:hover{color:#00CAF4}.app-section{display:flex;flex-direction:row;justify-content:center}.app-section .app-section-description{padding:60px;display:flex;flex-direction:column;justify-content:center}.app-section .app-section-description .app-section-description-title{font-family:"Source Sans Pro", Helvetica Neue, -apple-system, BlinkMacSystemFont, Helvetica, sans-serif}.app-section .app-section-description .app-section-description-extra{padding:30px 0;font-family:"Source Sans Pro", Helvetica Neue, -apple-system, BlinkMacSystemFont, Helvetica, sans-serif}.app-section .app-section-image{margin:60px;border-radius:10px;box-sizing:border-box}.app-image-container{position:relative;display:flex;flex-direction:column;align-items:flex-end}.app-image-container img{box-shadow:rgba(84,70,35,0.15) 0px 2px 8px,rgba(84,70,35,0.15) 0px 1px 3px;border-radius:10px}.side-tab{min-height:600px;margin-top:30px;background-color:white;flex:1 1;display:flex;flex-direction:row}.side-tab a{color:#777777;text-decoration:none}.side-tab .tabs{display:flex;flex-direction:column;flex-basis:300px;align-items:stretch}.side-tab .tabs .tab{text-align:center;margin:5px;border-radius:8px;padding:15px 0}.side-tab .tabs .tab.selected-true{background-color:#EEEEEE}.side-tab .tab-body{padding:20px}.feature-container{display:flex;flex-direction:column}.app-body{flex:1 1;padding:0 300px;display:flex;flex-direction:column}.app-download{display:flex;flex-direction:row}.app-download .download{display:flex;flex-direction:row;align-items:center;font-size:20px;color:white;padding:10px 25px;background-color:#363B3F;text-decoration:none;border-radius:15px}.app-download .download:hover{background-color:#2E3437}.app-download .download i.platform{padding-right:10px}.app-download .download .select{margin-left:10px;padding:0 3px;width:30px;text-align:center;display:flex;justify-content:center;border-radius:3px}.app-download .download .select:hover{background-color:#505050}.download-popover{display:flex;flex-direction:column;border-radius:5px;align-items:stretch;width:200px}.download-popover a{padding:10px;color:black;text-decoration:none}.download-popover a:hover{background-color:#7f7f7f;color:white}.download-popover a i{margin-right:10px}.App{height:100%;display:flex;flex-direction:column}html{background-color:#fff}

